@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,900;1,100;1,200;1,300;1,400;1,800;1,900&display=swap);

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
body {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
  background-color: #dddddd;
}

.product-container {
  position: relative;
  z-index: 100;
}

.product-container .product-card {
  position: relative;
  width: 300px;
  height: 550px;
  background: #ebf4f4;
  border-radius: 15px;
  overflow: hidden;
  margin: 30px;
}

.product-content-1 {
  position: relative;
  width: 100%;
  height: 35%;
  background-image: linear-gradient(to bottom right, #4f5b65, #65737f);
}

.product-content-1::after {
  content: "PLAY";
  position: absolute;
  left: 28%;
  top: 20%;
  font-size: 5.5em;
  font-weight: 800;
  color: #c1d4ec34;
  -webkit-text-fill-color: rgba(#fff, #fff, #fff, 0.231);
  -webkit-text-stroke-width: 2px;
}

.product-content-1 .heart {
  position: absolute;
  top: 8%;
  right: 6%;
  color: #e2e2e2;
  font-size: 20px;
  cursor: pointer;
}

.product-content .fa-heart {
  color: #f00c0c;
}

.product-content-1 .main-image img {
  width: 250px;
  height: 200px;
  position: absolute;
  top: 36%;
  left: 8%;
  z-index: 100;
}

.product-content-2 {
  height: 120px;
}

.product-content-2 .sub-image {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.product-content-2 .sub-image img {
  width: 45px;
  height: 45px;
  margin: 70px 7px 0px;
  padding: 5px;
  background: #fff;
  border-radius: 10px;
  box-shadow: -10px -10px 10px rgba(230, 244, 244, 0.5),
    5px 5px 5px rgba(70, 70, 70, 0.12);
  cursor: pointer;
  border: 1.5px solid #fff;
}

.product-content-2 .sub-image img:hover {
  border: 1.5px solid #0b67df;
}

.product-content-3 {
  position: relative;
  padding: 5px 20px 10px;
}

.branding span:nth-child(1) {
  font-size: 18px;
  font-weight: 400;
}

.branding h4 {
  font-size: 12px;
  font-weight: 500;
  color: #464444;
}

.ratings span {
  display: inline-block;
  text-align: center;
  line-height: 26px;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.5s;
  color: #fcba26;
}

.ratings span:nth-child(5) {
  color: #a7a7a3;
}

.paragraph p {
  font-size: 11.5px;
  color: #000;
  font-weight: 500;
}

.paragraph span {
  color: #0b67df;
  font-weight: 700;
  cursor: pointer;
}

.price {
  display: -webkit-flex;
  display: flex;
  margin: 30px 0 10px;
}

.space {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.price span {
  font-size: 25px;
  font-weight: 600;
}

.price span:nth-child(1) {
  font-size: 20px;
  font-weight: 400;
  margin-top: 3px;
  margin-right: 5px;
}

.price a {
  font-size: 12px;
  text-decoration: none;
  border: none;
  background-image: linear-gradient(to right, #0a52af, rgba(18, 112, 234));
  color: #fff;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 10px 30px;
  border-radius: 15px;
  cursor: pointer;
}

